.greeting {
  max-width: 754px;
  margin: 148px auto;
  &__title {
    color: $title-color;
    font-size: 32px;
    font-weight: 600;

    margin-bottom: 16px;
  }

  &__text {
    color: $main-text-color;
    font-size: 24px;
    font-family: "Open Sans", sans-serif;
    line-height: 150%;
  }
}
@media screen and (max-width: 960px) {
  .greeting {
    padding: 0 10px;
  }
}
